html {
  --color-brand: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, Arial, Helvetica, sans-serif;
}

ul, li {
  margin: 0;
  padding: 0;
  list-style: none;
}

.link {
  color: inherit;
  text-decoration: none;
  position: relative;
}

.container {
  padding: 0 50px;
}

.main-header__inner {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.main-header__logo {
  line-height: 0;
}

.main-header__nav-list {
  text-transform: uppercase;
  font-size: 12px;
  display: flex;
}

.main-header__nav-list li {
  margin: 0 10px;
}

.main-header__nav-list li:first-child {
  margin-left: 0;
}

.main-header__nav-list li:last-child {
  margin-right: 0;
}

.navbar-link {
  text-align: center;
  align-items: center;
  height: 60px;
  transition: color .2s ease-in-out;
  display: flex;
}

.navbar-link:hover, .navbar-link.is-active {
  color: var(--color-brand);
}

.navbar-link.is-active:after {
  content: "";
  background: var(--color-brand);
  border-radius: 8px;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
